"use client"

import * as React from "react"
import { AppSidebar } from "@/components/app-sidebar"
import {
  Breadcrumb,
  BreadcrumbItem,
  BreadcrumbLink,
  BreadcrumbList,
  BreadcrumbPage,
  BreadcrumbSeparator,
} from "@/components/ui/breadcrumb"
import { Button } from "@/components/ui/button"
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select"
import { Separator } from "@/components/ui/separator"
import {
  SidebarInset,
  SidebarProvider,
  SidebarTrigger,
} from "@/components/ui/sidebar"
import { Switch } from "@/components/ui/switch"
import { Textarea } from "@/components/ui/textarea"
import {
  Tabs,
  TabsContent,
  TabsList,
  TabsTrigger,
} from "@/components/ui/tabs"
import {
  QrCode,
  Image,
  Type,
  Globe,
  Tag,
  MapPin,
  Calendar,
  User,
  Download,
  Eye,
  RotateCw,
  Move,
  Trash2,
  Plus,
} from "lucide-react"

// Label Element Interface
interface LabelElement {
  id: string;
  type: 'qr' | 'text' | 'logo' | 'field';
  content: string;
  position: { x: number; y: number };
  size: { width: number; height: number };
  style: {
    fontSize?: number;
    fontWeight?: string;
    color?: string;
    alignment?: string;
  };
}

// Predefined field types
const fieldTypes = [
  { value: 'asset_id', label: 'Asset ID', icon: Tag },
  { value: 'asset_name', label: 'Asset Name', icon: Type },
  { value: 'ip_address', label: 'IP Adresse', icon: Globe },
  { value: 'location', label: 'Standort', icon: MapPin },
  { value: 'assigned_to', label: 'Zugewiesen an', icon: User },
  { value: 'date_created', label: 'Erstellungsdatum', icon: Calendar },
  { value: 'serial_number', label: 'Seriennummer', icon: Tag },
];

export default function QRLabelsPage() {
  const [labelElements, setLabelElements] = React.useState<LabelElement[]>([
    {
      id: '1',
      type: 'qr',
      content: 'ASSET-001',
      position: { x: 10, y: 10 },
      size: { width: 80, height: 80 },
      style: {},
    }
  ]);
  
  const [selectedElement, setSelectedElement] = React.useState<string | null>(null);
  const [labelSize, setLabelSize] = React.useState({ width: 200, height: 100 });
  const [qrContent, setQrContent] = React.useState('{{asset_id}}');
  const [includeText, setIncludeText] = React.useState(true);
  const [includeLogo, setIncludeLogo] = React.useState(false);
  const [draggedElement, setDraggedElement] = React.useState<string | null>(null);
  const [dragStart, setDragStart] = React.useState<{ x: number; y: number } | null>(null);

  // Add new element to label
  const addElement = (type: LabelElement['type'], content: string = '') => {
    const newElement: LabelElement = {
      id: Date.now().toString(),
      type,
      content,
      position: { x: 50, y: 50 },
      size: type === 'qr' ? { width: 60, height: 60 } : { width: 100, height: 20 },
      style: {
        fontSize: 12,
        fontWeight: 'normal',
        color: '#000000',
        alignment: 'left',
      },
    };
    setLabelElements([...labelElements, newElement]);
  };

  // Update element
  const updateElement = (id: string, updates: Partial<LabelElement>) => {
    setLabelElements(labelElements.map(el => 
      el.id === id ? { ...el, ...updates } : el
    ));
  };

  // Delete element
  const deleteElement = (id: string) => {
    setLabelElements(labelElements.filter(el => el.id !== id));
    setSelectedElement(null);
  };

  // Generate preview
  const generatePreview = () => {
    console.log("Generiere Vorschau für Label mit Elementen:", labelElements);
  };

  // Export labels
  const exportLabels = () => {
    console.log("Exportiere Labels als PDF");
  };

  const selectedEl = labelElements.find(el => el.id === selectedElement);

  // Handle mouse down for dragging
  const handleMouseDown = (e: React.MouseEvent, elementId: string) => {
    e.preventDefault();
    setSelectedElement(elementId);
    setDraggedElement(elementId);
    
    const rect = e.currentTarget.getBoundingClientRect();
    const parentRect = e.currentTarget.parentElement!.getBoundingClientRect();
    
    setDragStart({
      x: e.clientX - rect.left,
      y: e.clientY - rect.top
    });
  };

  // Handle mouse move for dragging
  const handleMouseMove = (e: React.MouseEvent) => {
    if (!draggedElement || !dragStart) return;

    const canvasRect = e.currentTarget.getBoundingClientRect();
    const newX = Math.max(0, Math.min((e.clientX - canvasRect.left - dragStart.x) / 2, labelSize.width - 20));
    const newY = Math.max(0, Math.min((e.clientY - canvasRect.top - dragStart.y) / 2, labelSize.height - 20));

    updateElement(draggedElement, {
      position: { x: newX, y: newY }
    });
  };

  // Handle mouse up to stop dragging
  const handleMouseUp = () => {
    setDraggedElement(null);
    setDragStart(null);
  };

  return (
    <SidebarProvider>
      <AppSidebar />
      <SidebarInset>
        <header className="flex h-16 shrink-0 items-center gap-2">
          <div className="flex items-center gap-2 px-4">
            <SidebarTrigger className="-ml-1" />
            <Separator
              orientation="vertical"
              className="mr-2 data-[orientation=vertical]:h-4"
            />
            <Breadcrumb>
              <BreadcrumbList>
                <BreadcrumbItem className="hidden md:block">
                  <BreadcrumbLink href="/admin">
                    Administration
                  </BreadcrumbLink>
                </BreadcrumbItem>
                <BreadcrumbSeparator className="hidden md:block" />
                <BreadcrumbItem>
                  <BreadcrumbPage>QR-Code Labels</BreadcrumbPage>
                </BreadcrumbItem>
              </BreadcrumbList>
            </Breadcrumb>
          </div>
        </header>

        <div className="flex flex-1 flex-col gap-4 p-4 pt-0">
          <div className="flex items-center justify-between mb-4">
            <h1 className="text-3xl font-semibold">QR-Code Label Generator</h1>
            <div className="flex gap-2">
              <Button variant="outline" onClick={generatePreview}>
                <Eye className="mr-2 h-4 w-4" />
                Vorschau
              </Button>
              <Button onClick={exportLabels}>
                <Download className="mr-2 h-4 w-4" />
                Exportieren
              </Button>
            </div>
          </div>

          <div className="grid grid-cols-1 lg:grid-cols-4 gap-6">
            {/* Element Toolbox */}
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Elemente</CardTitle>
                <CardDescription>
                  Elemente zum Label hinzufügen
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="space-y-2">
                  <Label className="text-sm font-medium">QR-Code</Label>
                  <Button 
                    variant="outline" 
                    className="w-full justify-start"
                    onClick={() => addElement('qr', qrContent)}
                  >
                    <QrCode className="mr-2 h-4 w-4" />
                    QR-Code hinzufügen
                  </Button>
                </div>

                <div className="space-y-2">
                  <Label className="text-sm font-medium">Datenfelder</Label>
                  {fieldTypes.map((field) => (
                    <Button
                      key={field.value}
                      variant="outline"
                      size="sm"
                      className="w-full justify-start"
                      onClick={() => addElement('field', `{{${field.value}}}`)}
                    >
                      <field.icon className="mr-2 h-3 w-3" />
                      {field.label}
                    </Button>
                  ))}
                </div>

                <div className="space-y-2">
                  <Label className="text-sm font-medium">Weitere Elemente</Label>
                  <Button
                    variant="outline"
                    size="sm"
                    className="w-full justify-start"
                    onClick={() => addElement('text', 'Beispieltext')}
                  >
                    <Type className="mr-2 h-3 w-3" />
                    Text hinzufügen
                  </Button>
                  <Button
                    variant="outline"
                    size="sm"
                    className="w-full justify-start"
                    onClick={() => addElement('logo', 'Logo')}
                  >
                    <Image className="mr-2 h-3 w-3" />
                    Logo hinzufügen
                  </Button>
                </div>
              </CardContent>
            </Card>

            {/* Label Canvas */}
            <Card className="lg:col-span-2">
              <CardHeader>
                <CardTitle className="text-lg">Label Designer</CardTitle>
                <CardDescription>
                  Größe: {labelSize.width} x {labelSize.height} mm
                </CardDescription>
              </CardHeader>
              <CardContent>
                <div className="flex justify-center">
                  <div
                    className="relative border-2 border-dashed border-gray-300 bg-white select-none"
                    style={{
                      width: `${labelSize.width * 2}px`,
                      height: `${labelSize.height * 2}px`,
                    }}
                    onMouseMove={handleMouseMove}
                    onMouseUp={handleMouseUp}
                    onMouseLeave={handleMouseUp}
                  >
                    {labelElements.map((element) => (
                      <div
                        key={element.id}
                        className={`absolute border ${
                          selectedElement === element.id 
                            ? 'border-blue-500 border-2' 
                            : 'border-gray-200'
                        } ${draggedElement === element.id ? 'cursor-grabbing' : 'cursor-grab'}`}
                        style={{
                          left: `${element.position.x * 2}px`,
                          top: `${element.position.y * 2}px`,
                          width: `${element.size.width * 2}px`,
                          height: `${element.size.height * 2}px`,
                        }}
                        onMouseDown={(e) => handleMouseDown(e, element.id)}
                      >
                        {element.type === 'qr' && (
                          <div className="w-full h-full bg-gray-900 flex items-center justify-center text-white text-xs pointer-events-none">
                            QR
                          </div>
                        )}
                        {element.type === 'text' && (
                          <div 
                            className="w-full h-full flex items-center p-1 text-xs pointer-events-none"
                            style={{ 
                              fontSize: `${(element.style.fontSize || 12) / 2}px`,
                              color: element.style.color,
                              textAlign: element.style.alignment as any,
                            }}
                          >
                            {element.content}
                          </div>
                        )}
                        {element.type === 'field' && (
                          <div 
                            className="w-full h-full flex items-center p-1 text-xs bg-blue-50 pointer-events-none"
                            style={{ 
                              fontSize: `${(element.style.fontSize || 12) / 2}px`,
                              color: element.style.color,
                              textAlign: element.style.alignment as any,
                            }}
                          >
                            {element.content.replace(/[{}]/g, '')}
                          </div>
                        )}
                        {element.type === 'logo' && (
                          <div className="w-full h-full bg-gray-100 flex items-center justify-center text-xs pointer-events-none">
                            Logo
                          </div>
                        )}
                        
                        {selectedElement === element.id && (
                          <Button
                            variant="destructive"
                            size="sm"
                            className="absolute -top-2 -right-2 h-6 w-6 p-0 z-10"
                            onClick={(e) => {
                              e.stopPropagation();
                              deleteElement(element.id);
                            }}
                            onMouseDown={(e) => e.stopPropagation()}
                          >
                            <Trash2 className="h-3 w-3" />
                          </Button>
                        )}
                      </div>
                    ))}
                  </div>
                </div>
              </CardContent>
            </Card>

            {/* Properties Panel */}
            <Card>
              <CardHeader>
                <CardTitle className="text-lg">Eigenschaften</CardTitle>
                <CardDescription>
                  {selectedEl ? 'Element bearbeiten' : 'Element auswählen'}
                </CardDescription>
              </CardHeader>
              <CardContent>
                <Tabs defaultValue="label" className="w-full">
                  <TabsList className="grid w-full grid-cols-2">
                    <TabsTrigger value="label">Label</TabsTrigger>
                    <TabsTrigger value="element">Element</TabsTrigger>
                  </TabsList>
                  
                  <TabsContent value="label" className="space-y-4">
                    <div className="space-y-2">
                      <Label>Label-Größe</Label>
                      <Select 
                        value={`${labelSize.width}x${labelSize.height}`}
                        onValueChange={(value) => {
                          const [width, height] = value.split('x').map(Number);
                          setLabelSize({ width, height });
                        }}
                      >
                        <SelectTrigger>
                          <SelectValue />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="200x100">200x100 mm</SelectItem>
                          <SelectItem value="150x75">150x75 mm</SelectItem>
                          <SelectItem value="100x50">100x50 mm</SelectItem>
                          <SelectItem value="80x40">80x40 mm</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>

                    <div className="space-y-2">
                      <Label>QR-Code Inhalt</Label>
                      <Textarea
                        placeholder="{{asset_id}} - {{asset_name}}"
                        value={qrContent}
                        onChange={(e) => setQrContent(e.target.value)}
                        rows={3}
                      />
                    </div>
                  </TabsContent>

                  <TabsContent value="element" className="space-y-4">
                    {selectedEl ? (
                      <>
                        <div className="space-y-2">
                          <Label>Inhalt</Label>
                          <Textarea
                            value={selectedEl.content}
                            onChange={(e) => updateElement(selectedEl.id, { content: e.target.value })}
                            rows={2}
                          />
                        </div>

                        <div className="grid grid-cols-2 gap-2">
                          <div className="space-y-2">
                            <Label>X Position</Label>
                            <Input
                              type="number"
                              value={selectedEl.position.x}
                              onChange={(e) => updateElement(selectedEl.id, {
                                position: { ...selectedEl.position, x: Number(e.target.value) }
                              })}
                            />
                          </div>
                          <div className="space-y-2">
                            <Label>Y Position</Label>
                            <Input
                              type="number"
                              value={selectedEl.position.y}
                              onChange={(e) => updateElement(selectedEl.id, {
                                position: { ...selectedEl.position, y: Number(e.target.value) }
                              })}
                            />
                          </div>
                        </div>

                        <div className="grid grid-cols-2 gap-2">
                          <div className="space-y-2">
                            <Label>Breite</Label>
                            <Input
                              type="number"
                              value={selectedEl.size.width}
                              onChange={(e) => updateElement(selectedEl.id, {
                                size: { ...selectedEl.size, width: Number(e.target.value) }
                              })}
                            />
                          </div>
                          <div className="space-y-2">
                            <Label>Höhe</Label>
                            <Input
                              type="number"
                              value={selectedEl.size.height}
                              onChange={(e) => updateElement(selectedEl.id, {
                                size: { ...selectedEl.size, height: Number(e.target.value) }
                              })}
                            />
                          </div>
                        </div>

                        {(selectedEl.type === 'text' || selectedEl.type === 'field') && (
                          <>
                            <div className="space-y-2">
                              <Label>Schriftgröße</Label>
                              <Input
                                type="number"
                                value={selectedEl.style.fontSize || 12}
                                onChange={(e) => updateElement(selectedEl.id, {
                                  style: { ...selectedEl.style, fontSize: Number(e.target.value) }
                                })}
                              />
                            </div>

                            <div className="space-y-2">
                              <Label>Textfarbe</Label>
                              <Input
                                type="color"
                                value={selectedEl.style.color || '#000000'}
                                onChange={(e) => updateElement(selectedEl.id, {
                                  style: { ...selectedEl.style, color: e.target.value }
                                })}
                              />
                            </div>

                            <div className="space-y-2">
                              <Label>Ausrichtung</Label>
                              <Select
                                value={selectedEl.style.alignment || 'left'}
                                onValueChange={(value) => updateElement(selectedEl.id, {
                                  style: { ...selectedEl.style, alignment: value }
                                })}
                              >
                                <SelectTrigger>
                                  <SelectValue />
                                </SelectTrigger>
                                <SelectContent>
                                  <SelectItem value="left">Links</SelectItem>
                                  <SelectItem value="center">Mitte</SelectItem>
                                  <SelectItem value="right">Rechts</SelectItem>
                                </SelectContent>
                              </Select>
                            </div>
                          </>
                        )}
                      </>
                    ) : (
                      <p className="text-sm text-muted-foreground">
                        Wählen Sie ein Element aus, um es zu bearbeiten.
                      </p>
                    )}
                  </TabsContent>
                </Tabs>
              </CardContent>
            </Card>
          </div>
        </div>
      </SidebarInset>
    </SidebarProvider>
  )
}
